import React from 'react';
import './index.less';
// API:
// children -> nolimited
// onClick -> Function 
// type, primary,danger,Button.less -> String
// style -> Object
const Button  = ({children,onClick,type,style}) =>{
  return (
    <div className={`gy-btn ${type || `primary`}`} onClick={onClick} style={style}>{children}</div>
  );
};
export default Button;